'use client';
import React, { useState } from 'react';
import DSASidebar from '@/components/DSASidebar/DSASidebar';
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  InputBase,
  Box,
  Paper,
  Grid,
  useTheme,
  useMediaQuery,
  Drawer
} from '@mui/material';
import {
  Menu as MenuIcon,
  Search as SearchIcon,
  AccountCircle as AccountCircleIcon,
  Book as BookIcon
} from '@mui/icons-material';

export default function DashboardPage() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [mobileOpen, setMobileOpen] = useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = <DSASidebar />;

  return (
    <Box sx={{ display: 'flex', height: '100vh' }}>
      {/* App Bar */}
      <AppBar
        position="fixed"
        sx={{
          width: { md: `calc(100% - 320px)` },
          ml: { md: '320px' },
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { md: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ display: { xs: 'none', sm: 'block' } }}
          >
            DSA Tracker
          </Typography>
          
          <Box sx={{ flexGrow: 1 }} />
          
          <Paper
            component="form"
            sx={{
              p: '2px 4px',
              display: 'flex',
              alignItems: 'center',
              width: { xs: '100%', sm: 'auto' },
              maxWidth: 400,
              mr: 2
            }}
          >
            <IconButton type="button" sx={{ p: '10px' }} aria-label="search">
              <SearchIcon />
            </IconButton>
            <InputBase
              sx={{ ml: 1, flex: 1 }}
              placeholder="Search problems..."
              inputProps={{ 'aria-label': 'search problems' }}
            />
          </Paper>
          
          <IconButton
            size="large"
            edge="end"
            aria-label="account of current user"
            color="inherit"
          >
            <AccountCircleIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      {/* Sidebar */}
      <Box
        component="nav"
        sx={{ width: { md: 320 }, flexShrink: { md: 0 } }}
        aria-label="mailbox folders"
      >
        {isMobile ? (
          <Drawer
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            ModalProps={{ keepMounted: true }}
            sx={{
              display: { xs: 'block', md: 'none' },
              '& .MuiDrawer-paper': { boxSizing: 'border-box', width: 320 },
            }}
          >
            {drawer}
          </Drawer>
        ) : (
          <Drawer
            variant="permanent"
            sx={{
              display: { xs: 'none', md: 'block' },
              '& .MuiDrawer-paper': { boxSizing: 'border-box', width: 320, position: 'relative' },
            }}
            open
          >
            {drawer}
          </Drawer>
        )}
      </Box>

      {/* Main Content */}
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          width: { md: `calc(100% - 320px)` },
          mt: '64px',
          height: 'calc(100vh - 64px)',
          overflow: 'auto',
        }}
      >
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Paper
              sx={{
                p: 4,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                textAlign: 'center',
              }}
            >
              <BookIcon
                color="action"
                sx={{ fontSize: 48, mb: 2, color: 'text.secondary' }}
              />
              <Typography component="h3" variant="h5" gutterBottom>
                Welcome to DSA Tracker
              </Typography>
              <Typography variant="body1" color="text.secondary" paragraph>
                Select a problem from the sidebar to get started.
              </Typography>
            </Paper>
          </Grid>

          {/* Progress Cards */}
          <Grid item xs={12} md={4}>
            <Paper
              sx={{
                p: 3,
                display: 'flex',
                flexDirection: 'column',
                height: '100%',
                background: theme.palette.mode === 'dark' 
                  ? 'linear-gradient(45deg, #0d47a1 30%, #1976d2 90%)' 
                  : 'linear-gradient(45deg, #e3f2fd 30%, #bbdefb 90%)',
              }}
            >
              <Typography
                component="h3"
                variant="subtitle1"
                color={theme.palette.mode === 'dark' ? 'common.white' : 'primary.dark'}
                gutterBottom
              >
                Total Problems
              </Typography>
              <Typography
                component="p"
                variant="h4"
                color={theme.palette.mode === 'dark' ? 'common.white' : 'primary.dark'}
                sx={{ fontWeight: 'bold' }}
              >
                0
              </Typography>
            </Paper>
          </Grid>

          <Grid item xs={12} md={4}>
            <Paper
              sx={{
                p: 3,
                display: 'flex',
                flexDirection: 'column',
                height: '100%',
                background: theme.palette.mode === 'dark' 
                  ? 'linear-gradient(45deg, #1b5e20 30%, #2e7d32 90%)' 
                  : 'linear-gradient(45deg, #e8f5e9 30%, #c8e6c9 90%)',
              }}
            >
              <Typography
                component="h3"
                variant="subtitle1"
                color={theme.palette.mode === 'dark' ? 'common.white' : 'success.dark'}
                gutterBottom
              >
                Completed
              </Typography>
              <Typography
                component="p"
                variant="h4"
                color={theme.palette.mode === 'dark' ? 'common.white' : 'success.dark'}
                sx={{ fontWeight: 'bold' }}
              >
                0
              </Typography>
            </Paper>
          </Grid>

          <Grid item xs={12} md={4}>
            <Paper
              sx={{
                p: 3,
                display: 'flex',
                flexDirection: 'column',
                height: '100%',
                background: theme.palette.mode === 'dark' 
                  ? 'linear-gradient(45deg, #e65100 30%, #ef6c00 90%)' 
                  : 'linear-gradient(45deg, #fff3e0 30%, #ffe0b2 90%)',
              }}
            >
              <Typography
                component="h3"
                variant="subtitle1"
                color={theme.palette.mode === 'dark' ? 'common.white' : 'warning.dark'}
                gutterBottom
              >
                In Progress
              </Typography>
              <Typography
                component="p"
                variant="h4"
                color={theme.palette.mode === 'dark' ? 'common.white' : 'warning.dark'}
                sx={{ fontWeight: 'bold' }}
              >
                0
              </Typography>
            </Paper>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
}